//动态背景
$('body').on('mousemove', function(event) {
	var x = -((event.clientX - 960) / 40) - 220 + 'px';
	var y = -((event.clientY - 525) / 50) -50+ 'px';
	$('#app').css({
		'background-position-x': x,
		'background-position-y': y
	})
})


